.nav{
  background-color: #393E46;
  padding-left: 5rem;
  font-size: 1.75rem;
}

.nav li {
  color: #00adb5;
  display: inline-block;
}

.nav li:hover {
  background-color: #222831;
  color: #EEEEEE;
}

.nav a{
  display: inline-block;
  padding: 2rem;
  color: inherit;
  text-decoration: none;
}



